* {
    margin: 0;
    padding: 0;
    list-style: none;
    box-sizing: border-box;
    text-decoration: none;
    font-style: normal;
    white-space: normal;
    word-break: break-all;
}

button {
    outline: none;
    cursor: pointer;
    border: none;
    background: none;
    font-family: "NanumSquareRound";
    vertical-align: top;
    display: inline-block;
}

input {}

input:focus {
    ouline: none;
    border: none;
}

input::-webkit-input-placeholder {
    color: #AEAEAE;
}

input:-ms-input-placeholder {
    color: #AEAEAE;
}

textarea::-webkit-input-placeholder {
    color: #AEAEAE;
}

textarea:-ms-input-placeholder {
    color: #AEAEAE;
}

select {
    background-size: 12px;
    background-position-x: 95%;
    border-radius: 0;
    border: solid 1px #F2F3F5;
    outline: none;
    transition: all 0.2s ease-in-out;
    padding: 0 10px;
    font-family: "NanumSquareRound";
    color: #111;
    font-size: 15px;
    -webkit-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    vertical-align: top;
}

select::-ms-expand {
    display: none;
}

select:focus {
    border: solid 1px #FF6001;
    background-size: 12px;
    background-position-x: 95%;
}

.calendar:focus {
    background-size: 12px;
    background-position-x: 95%;
}

textarea {
    width: 100%;
    height: 80px;
    background: #F2F3F5;
    border-radius: 0;
    border: solid 1px #F2F3F5;
    outline: none;
    transition: all 0.2s ease-in-out;
    font-family: "NanumSquareRound";
    color: #111;
    font-size: 15px;
    padding: 8px 10px;
    line-height: 24px;
}

textarea:focus {
    border: solid 1px #FF6001;
    background: #fff;
}

.container {
    width: 100%;
    min-width: 280px;
    min-height: 100%;
    background: #F2F3F5;
    position: relative;
    z-index: 0;
}

.contents {
    max-width: 1320px;
    min-height: 77vh;
    margin: 0 auto;
    padding: 0 20px 120px 20px;
}

/*header style*/
.header {
    height: 0;
}

.headerWrap {
    background: #fff;
    font-family: "NanumSquareRound";
    color: #111;
    position: relative;
}

.logoTop {
    width: 235px;
    height: 120px;
    background: url(/static/media/logo_top.099b19a7.svg) center no-repeat;
    background-size: contain;
    margin-left: 60px;
    margin-top: 20px;
    position: absolute;
    z-index: 200;
    cursor: pointer;
}

.headerRight {
    position: absolute;
    right: 60px;
    top: 20px;
    z-index: 200;
}

.sns {
    border-radius: 30px;
    background: #fff;
    padding: 10px 30px;
    display: inline-block;
    vertical-align: top;
    margin-right: 10px;
    box-shadow: 0px 1px 10px rgba(0,0,0,0.2);
}

.sns > a {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: inline-block;
    vertical-align: top;
}

.twitter {
    background: url(/static/media/twitter.a8f91f06.svg)#1d9bf0 center no-repeat;
    background-size: contain;
    margin-right: 20px;
}

.discord {
    background: url(/static/media/discord.ffc343df.svg)#8b9eff center no-repeat;
    background-size: contain;
    margin-right: 20px;
}

.gmail {
    background: url(/static/media/google.0de8c67d.svg)#fff center no-repeat;
    background-size: contain;
}

.nav {
    border-radius: 30px;
    background: #fff;
    padding: 10px 30px;
    display: inline-block;
    vertical-align: top;
    box-shadow: 0px 1px 10px rgba(0,0,0,0.2);
}

.nav .guide {
    display: inline-block;
    vertical-align: top;
    margin-right: 30px;
}

.nav .lang {
    display: inline-block;
    vertical-align: top;
}

.navIcon {
    display: inline-block;
    vertical-align: top;
    width: 40px;
    height: 40px;
}

.guide .navIcon {
    background: url(/static/media/guide.b499079b.svg) center no-repeat;
    background-size: 30px;
}

.lang .navIcon {
    background: url(/static/media/language.43f246ae.svg) center no-repeat;
    background-size: 30px;
}

.navText {
    font-size: 17px;
    line-height: 40px;
    text-align: center;
    font-family: "NanumSquareRound";
    color: #222;
    display: inline-block;
    vertical-align: top;
}

.nav select,
.moNav select {
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: 20px;
    background-position: 100%;
    background-color: #fff;
    font-size: 17px;
    line-height: 38px;
    text-align: left;
    padding: 0 10px;
    font-family: "NanumSquareRound";
    color: #222;
    width: 100px;
}

.nav select:focus,
.moNav select:focus {
    border: solid 1px #222;
}

.hamburger {
    width: 45px;
    height: 45px;
    vertical-align: top;
    border-radius: 50%;
    position: relative;
    display: none;
    box-shadow: 0px 1px 10px rgba(0,0,0,0.2);
    background: url(/static/media/iconHamburger.04e72609.svg)#fff center no-repeat;
    background-size: 20px;
}
.close{
    width: 45px;
    height: 45px;
    border-radius: 50%;
    position: absolute;
    background: url(/static/media/iconClose.51c73e22.svg)#fff center no-repeat;
    background-size: 18px;
    top: -55px;
    right: 0;
    z-index: 50;
}

.submenu {
    position: absolute;
    top: 55px;
    right: 0;
    width: 230px;
    display: none;
    transition: 0.5s ease;
    z-index: 20;
}

.moNav {
    border-radius: 30px;
    background: #fff;
    padding: 30px 30px;
    display: block;
    vertical-align: top;
    box-shadow: 0px 5px 20px rgba(0,0,0,0.15);
}

.moNav .guide {
    display: block;
    vertical-align: top;
    margin-bottom: 30px;
    width: 100%;
}

.moNav .lang {
    display: block;
    vertical-align: top;
    width: 100%;
}

.moSns {
    border-radius: 30px;
    background: #fff;
    padding: 10px 30px;
    display: block;
    vertical-align: top;
    margin-top: 10px;
    text-align: center;
    box-shadow: 0px 5px 20px rgba(0,0,0,0.15);
}

.moSns > a {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: inline-block;
    vertical-align: top;
}

/*footer style*/
#footer {
    width: 100%;
    background: #F2F3F5;
}

.footerWrap {
    width: 100%;
    background: #F2F3F5;
}

.footerArea {
    max-width: 1320px;
    margin: 0 auto;
    padding: 0 20px;
    text-align: center;
}

.logoBottom {
    width: 100px;
    height: 50px;
    background: url(/static/media/logo_bottom.95b1e34b.svg) center no-repeat;
    background-size: contain;
    margin-top: 40px;
}

.footerBottom {
    text-align: center;
    font-size: 17px;
    line-height: 24px;
    font-weight: 500;
    margin-top: 10px;
    color: #111;
    font-family: "NanumSquareRound";
    padding-bottom: 70px;
}

@media all and (max-width:1319px) {
    .logoTop {
        margin-left: 20px;
        width: 157px;
        height: 80px;
    }

    .nav,
    .sns {
        display: none;
    }

    .headerRight {
        right: 20px;
    }

    .hamburger {
        display: block;
    }

}
